
.door {
    height: 250px;
    width: 150px;
    background-color: rgb(189, 189, 189);
    cursor: pointer;
}

.door .knob {
    background: black;
    border-radius: 100%;
    height: 10px;
    width: 10px;
    position: relative;
    margin-top: 70px;
    margin-left: 100px;
}
.button {
    display: inline-block;
    border-radius: 50px;
    background-color: #99c942;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 28px;
    padding: 10px;
    width: 150px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
}

.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.button span:after {
    content: '\BB';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.button:hover span {
    padding-right: 25px;
}

.button:hover span:after {
    opacity: 1;
    right: 0;
}

